<template>
	<view :style="temp">
		<view class="padding-xl">
			<!-- logo -->
			
			<view class="login-logo">数智平台</view>
			<view class="loginMode">
				<text :class="{activeSty: isActive, endSty: isend}"
					@tap="passwordLoginFun">密码登录</text>
				<text :class="{endSty: isActiveVer, activeSty: isEndVer}"
					@tap="verificationLoginFun">验证码登录</text>
			</view>
			<view class="cu-form-group flex" v-show="pasBox">
				<image class="typeImg" src="">
				</image>
				<input class="flex1" autocomplete="off" placeholder="请输入账号" v-model="phoneNumber"></input>
			</view>
			<view class="cu-form-group flex" v-show="!pasBox">
				<image class="typeImg" src="">
				</image>
				<input class="flex1" autocomplete="off" placeholder="请输入账号" type="number"
					v-model="phoneNumber" @blur="doInput(phoneNumber)" maxlength="11"></input>
			</view>

			<view class="cu-form-group flex" v-show="pasBox">
				<image class="typeImg" src=""></image>
				<input autocomplete="new-password" class="login-input flex1" placeholder-style="color:grey"
					placeholder="请输入密码" v-model="loginPassword" :type="inputType" />
				<uni-icons @click="closedEyeIcon" :type="customVariable? 'eye' : 'eye-slash'" style="line-height: 35px;" size="14" color="rgb(192, 196, 204)" />
			</view>
			<view class="cu-form-group flex" v-show="!pasBox">
				<image class="typeImg" src="">
					<input class="flex1" autocomplete="off" placeholder="请输入验证码" v-model="verification"></input>
					<text v-if="send" class="send" @tap="getValidation">发送验证码</text>
					<view v-else class="iblock">
						<text class="send">{{ textButton }}s</text>
						<text class="send">后重新发送</text>
					</view>

			</view>

			<!-- 登录按钮 -->
			<button :style="tempBorder" class="nextStepBtn wpd100" @tap="getRoleDataList">登录</button>

		
		</view>

	</view>
</template>

<script>
	const app = getApp();

	import {
		getUser,
		getThreecheckphoneandsend,
		getLogin,
		getCodeLogin,
		getRoleList
	} from '../../utils/http.js';
	
	export default {
		data() {
			return {
				temp: '--webColor: #000000;',
				tempBorder: '--webBorder: 0px;',
				wechatImg: app.globalData.imgUrl + '/xxxx.png',
				phoneNumber: '',
				isRegister:1,
				token:'',
				shopId: app.globalData.shopId,
				appId: app.globalData.appId,
				shopName: app.globalData.shopName,
				loginPassword: '',
				verification: '',
				businessSwitchShow: false,
				isActive: true,
				activityId: '0',
				pageId: 0,
				isend: false,
				isActiveVer: true,
				isEndVer: false,
				pasBox: true,
				textButton: "",
				eyeIcon: 'icon-yincang',
				customVariable: true,
				inputType: 'password',
				send: true,
				logoIcon: '',
				roleList: [],
				wxCode: '',
				identificationCode: ''
			}
		},
		async onReady() {
			let ua = window.navigator.userAgent.toLowerCase();
			if (ua.match(/wxwork/i) == "wxwork") {
				this.getWxwork()
			}
		},
		async onShow() {},
		computed: {},
		async onLoad(options) {},
		mounted() {
			
		},
		methods: {
			// 企业用户直接登录
			getWxwork(){
				getLogin({
					userName: '15756165920',
					password: 'Ksjx0905',
					loginPlatform: '5',
					shopId: 8
				}).then((res) => {
					if (res.code == 1) {
						uni.setStorage({
							key: 'token',
							data: res.data.token
						});		
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none',
							duration: 3000
						});
						return
					}
				})
			},
			// 登录成功获取用户信息
			getUserInfoFn(){
				var that =this
				uni.setStorage({
					key: 'token',
					data: that.token
				});
				getUser({token: that.token}).then((res) => {
					if (res.code == 1) {
						uni.reLaunch({
							url: "/pages/index/index"
						})
					}
				})								
			},
			getRoleDataList(){
				if (this.phoneNumber == '') {
					uni.showToast({
						title: '请输入手机号',
						icon: 'none'
					});
					return false;
				}
				getRoleList().then((res) => {
					const roleList = JSON.parse(res.data)
					var newrr = roleList.filter(item => item.phone == this.phoneNumber)
					
					if (newrr.length == 0) {
						uni.showToast({
							title: '您没有查看此项目的权限',
							icon: 'none'
						});
						return false;
					}
					this.signInFun()
				})
			},
			// 点击密码登录
			passwordLoginFun: function() {
				this.isActive = true,
					this.isend = false,
					this.isActiveVer = true,
					this.isEndVer = false,
					this.pasBox = true
			},

			// 点击验证码登录
			verificationLoginFun: function() {
				this.isActive = false,
					this.isend = true,
					this.isActiveVer = false,
					this.isEndVer = true,
					this.pasBox = false
			},

			// 手机号验证
			doInput: function(val) {
				let regPhone = (/^(13[0-9]|14[1579]|15[0-3,5-9]|16[6]|17[0123456789]|18[0-9]|19[89])\d{8}$/);
				if (!regPhone.test(val)) {
					uni.showToast({
						title: '手机号格式错误',
						icon: 'none'
					});
					return;
				}
			},

			// 获取验证码
			getValidation: function() {
				var that = this
				if (this.phoneNumber == '') {
					uni.showToast({
						title: '请输入手机号',
						icon: 'none'
					});
					return false;
				}
				
				
				getThreecheckphoneandsend({
					userMobile: this.phoneNumber,
					shopId: 8
				}).then((res) => {
					if (res.code == 1) {
						let _this = this
						let timer;
						let seconds = 60;
						clearInterval(timer);
						timer = setInterval(function() {
							if (seconds > 0) {
								_this.send = false;
								_this.textButton = seconds;
								_this.$forceUpdate();
								seconds--
							} else {
								_this.send = true;
								clearInterval(timer)
							}
						}, 1000)
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none',
							duration: 3000
						})
					}
				})
			},

			// 点击眼睛图标
			closedEyeIcon: function() {
				if (this.customVariable) {
					this.customVariable = false
						this.inputType = 'text'
				} else {
					this.customVariable = true
						this.inputType = 'password'
					
				}
			},


			// 登录按钮
			signInFun: function async() {
				var that = this
				if (!this.phoneNumber) {
					uni.showToast({
						title: '请输入手机号',
						icon: 'none'
					});
					return false
				}

				// 密码登录
				if (this.pasBox) {
					if (!this.loginPassword) {
						uni.showToast({
							title: '请输入密码',
							icon: 'none'
						});
						return false
					}
					
					getLogin({
						userName: this.phoneNumber,
						password: this.loginPassword,
						loginPlatform: '5',
						shopId: 8
					}).then((res) => {
						if (res.code == 1) {
							that.token = res.data.token
							that.getUserInfoFn()			
						} else {
							uni.showToast({
								title: res.message,
								icon: 'none',
								duration: 3000
							});
							return
						}
					})
			
				} else {
					// 验证码登录
					if (!this.verification) {
						uni.showToast({
							title: '请输入验证码',
							icon: 'none'
						});
						return false
					}
					getCodeLogin({
						userMobile: this.phoneNumber,
						phoneCode: this.verification,
						shopId: 8,
						platForm: 2
					}).then((res) => {
						if (res.code == 1) {
							that.token = res.data.token
							that.getUserInfoFn()			
						} else {
							uni.showToast({
								title: res.message,
								icon: 'none',
								duration: 3000
							});
							return
						}
					})
					
				}

			}
		}
	}
</script>

<style>
	@import url("./landingPage.css");

	.login-input {
		outline: none;
		padding: 0;
		border: 0;
		flex: 1;
		height: 5vh;
		line-height: 5vh;
	}

	.c-themeColor {
		color: var(--webColor);
	}
</style>